.list {
  display: flex;
  justify-content: space-between;
  list-style-type: none;
  padding-left: 0;
}

.element {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.count {
  width: 25px;
  height: 25px;
  border: 2px solid white;
  border-radius: 50%;
  line-height: 25px;
  text-align: center;
  background-color: rgb(230, 230, 230);
}